@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  font-family: -apple-system;
  font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;
  text-rendering: optimizeLegibility;
  -webkit-backface-visibility: hidden;
  -webkit-user-drag: none;
  /*font-family: "Microsoft YaHei", "微软雅黑", Helvetica, Tahoma,Arial, SimSun, "宋体", STXihei, "华文细黑", Heiti, "黑体", sans-serif;*/
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  /* 字体抗锯齿 */
  cursor: pointer; }

html, body {
  font-family: -apple-system;
  font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif; }

a {
  text-decoration: none; }

em {
  font-style: normal; }

input, textarea {
  margin: 0;
  padding: 0;
  border: none;
  outline: none; }

input[type=number] {
  -moz-appearance: textfield; }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; }

input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px white inset; }

::-webkit-input-placeholder, ::-webkit-textarea-placeholder {
  margin: 0;
  padding: 0;
  color: #8c8c8c; }

@media screen and (min-width: 1600px) {
  html, body {
    font-size: 22px; } }

@media screen and (min-width: 1400px) and (max-width: 1600px) {
  html, body {
    font-size: 20px; } }

@media screen and (min-width: 1200px) and (max-width: 1400px) {
  html, body {
    font-size: 18px; } }

@media screen and (min-width: 992px) and (max-width: 1200px) {
  html, body {
    font-size: 17px; } }

@media screen and (min-width: 768px) and (max-width: 992px) {
  html, body {
    font-size: 17px; } }

@media screen and (max-width: 768px) {
  html, body {
    font-size: 17px; } }

/* 定义适配基础单位 */
/* 定义px转rem规则 */
/* 定义间距尺寸 */
:root {
  --head-top: #f5f5f5;
  --head-tab: #ffffff;
  --head-name: #141414;
  --head-tips: #262626; }

.header-panel {
  width: 100%; }
  .header-panel .header-top {
    background-color: var(--head-top);
    height: 1.75rem; }
    .header-panel .header-top .header-tips {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 0 auto;
      width: 61.25rem; }
      .header-panel .header-top .header-tips h1 {
        margin: 0 0 0 1rem;
        font-size: 0.75rem;
        font-weight: 400;
        color: #262626;
        color: var(--head-tips); }
      .header-panel .header-top .header-tips .header-interact {
        position: relative;
        height: 1.75rem;
        display: flex;
        justify-content: center;
        align-items: center; }
        .header-panel .header-top .header-tips .header-interact .header-icon {
          width: auto;
          height: 1.375rem; }
        .header-panel .header-top .header-tips .header-interact .header-number {
          margin-left: 0.5rem;
          font-size: 1rem;
          font-weight: 400;
          color: #141414;
          color: var(--head-tel);
          font-weight: bold; }
  .header-panel .header-tabber {
    border-bottom: 1px solid #d9d9d9;
    border-color: var(--head-border);
    background-color: var(--head-tab); }
    .header-panel .header-tabber .header-line {
      display: flex;
      flex-direction: row;
      margin: 0 auto;
      width: 61.25rem;
      height: 3.125rem; }
      .header-panel .header-tabber .header-line .header-logo {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 1.75rem;
        width: 8rem; }
        .header-panel .header-tabber .header-line .header-logo img {
          width: 100%;
          height: auto; }
        .header-panel .header-tabber .header-line .header-logo .ming-dong {
          display: flex;
          flex-direction: column; }
          .header-panel .header-tabber .header-line .header-logo .ming-dong h1 {
            margin-top: 4.25rem;
            font-size: 1.375rem;
            font-weight: 500;
            color: #262626;
            text-align: center;
            margin: 0;
            line-height: 30px;
            color: #ffffff;
            transform: scale(1, 0.84); }
          .header-panel .header-tabber .header-line .header-logo .ming-dong span {
            font-size: 0.75rem;
            font-weight: 400;
            color: #262626;
            color: #ffffff;
            transform: scale(0.82); }
      .header-panel .header-tabber .header-line .header-tab {
        display: flex;
        flex-direction: row; }
        .header-panel .header-tabber .header-line .header-tab .header-item {
          display: flex;
          justify-content: center;
          align-items: center; }
          .header-panel .header-tabber .header-line .header-tab .header-item a {
            padding: 0 0.875rem;
            font-size: 0.875rem;
            font-weight: 400;
            color: #141414;
            white-space: nowrap;
            color: var(--head-name); }
            .header-panel .header-tabber .header-line .header-tab .header-item a:hover {
              color: var(--head-hover); }
        .header-panel .header-tabber .header-line .header-tab .item-active {
          position: relative; }
          .header-panel .header-tabber .header-line .header-tab .item-active a {
            color: var(--head-hover); }
        .header-panel .header-tabber .header-line .header-tab .item-active::after {
          position: absolute;
          content: '';
          top: 0;
          left: 0;
          width: 100%;
          height: 0.125rem;
          background-color: var(--head-hover); }
