@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  font-family: -apple-system;
  font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;
  text-rendering: optimizeLegibility;
  -webkit-backface-visibility: hidden;
  -webkit-user-drag: none;
  /*font-family: "Microsoft YaHei", "微软雅黑", Helvetica, Tahoma,Arial, SimSun, "宋体", STXihei, "华文细黑", Heiti, "黑体", sans-serif;*/
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  /* 字体抗锯齿 */
  cursor: pointer; }

html, body {
  font-family: -apple-system;
  font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif; }

a {
  text-decoration: none; }

em {
  font-style: normal; }

input, textarea {
  margin: 0;
  padding: 0;
  border: none;
  outline: none; }

input[type=number] {
  -moz-appearance: textfield; }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; }

input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px white inset; }

::-webkit-input-placeholder, ::-webkit-textarea-placeholder {
  margin: 0;
  padding: 0;
  color: #8c8c8c; }

@media screen and (min-width: 1600px) {
  html, body {
    font-size: 22px; } }

@media screen and (min-width: 1400px) and (max-width: 1600px) {
  html, body {
    font-size: 20px; } }

@media screen and (min-width: 1200px) and (max-width: 1400px) {
  html, body {
    font-size: 18px; } }

@media screen and (min-width: 992px) and (max-width: 1200px) {
  html, body {
    font-size: 17px; } }

@media screen and (min-width: 768px) and (max-width: 992px) {
  html, body {
    font-size: 17px; } }

@media screen and (max-width: 768px) {
  html, body {
    font-size: 17px; } }

/* 定义适配基础单位 */
/* 定义px转rem规则 */
/* 定义间距尺寸 */
:root {
  --head-top: #f5f5f5;
  --head-tab: #ffffff;
  --head-name: #141414;
  --head-tips: #262626; }

.footer-box {
  width: 100%;
  height: auto;
  background-color: #262626; }
  .footer-box .footer-relate {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    padding: 1.75rem 0;
    min-width: 61.25rem; }
    .footer-box .footer-relate .relate-item {
      display: flex;
      flex-direction: row; }
      .footer-box .footer-relate .relate-item .relate-list {
        margin: 0 3rem 0 0; }
        .footer-box .footer-relate .relate-item .relate-list h5 {
          margin-top: 0.5rem;
          font-size: 1rem;
          font-weight: 500;
          color: #434343;
          text-align: center;
          text-align: left;
          font-size: 0.75rem;
          color: #f5f5f5; }
        .footer-box .footer-relate .relate-item .relate-list .relate-link {
          display: flex;
          flex-direction: column; }
          .footer-box .footer-relate .relate-item .relate-list .relate-link a {
            margin: 0.25rem 0 0 0;
            font-size: 0.6875rem;
            color: #bfbfbf;
            font-weight: 400; }
          .footer-box .footer-relate .relate-item .relate-list .relate-link a:first-child {
            margin: 0.5rem 0 0 0; }
          .footer-box .footer-relate .relate-item .relate-list .relate-link a:hover {
            color: #f5f5f5; }
    .footer-box .footer-relate .relate-other .relate-ercode {
      width: 5.625rem;
      height: 5.625rem;
      background-color: gray; }
    .footer-box .footer-relate .relate-other .serve-item {
      margin-top: 1.125rem;
      font-size: 0.875rem;
      color: #f5f5f5;
      font-weight: 500; }
  .footer-box .footer-owner {
    min-width: 61.25rem;
    padding: 0.75rem 1rem;
    border-top: 1px solid #bfbfbf;
    text-align: center; }
    .footer-box .footer-owner div {
      margin: 0.125rem 0 0 0;
      font-size: 0.625rem;
      color: #bfbfbf;
      font-weight: 400; }
      .footer-box .footer-owner div span, .footer-box .footer-owner div a {
        font-size: 0.625rem;
        color: #bfbfbf;
        font-weight: 400; }
      .footer-box .footer-owner div a:hover {
        color: #f5f5f5; }
