@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  font-family: -apple-system;
  font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;
  text-rendering: optimizeLegibility;
  -webkit-backface-visibility: hidden;
  -webkit-user-drag: none;
  /*font-family: "Microsoft YaHei", "微软雅黑", Helvetica, Tahoma,Arial, SimSun, "宋体", STXihei, "华文细黑", Heiti, "黑体", sans-serif;*/
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  /* 字体抗锯齿 */
  cursor: pointer; }

html, body {
  font-family: -apple-system;
  font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif; }

a {
  text-decoration: none; }

em {
  font-style: normal; }

input, textarea {
  margin: 0;
  padding: 0;
  border: none;
  outline: none; }

input[type=number] {
  -moz-appearance: textfield; }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; }

input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px white inset; }

::-webkit-input-placeholder, ::-webkit-textarea-placeholder {
  margin: 0;
  padding: 0;
  color: #8c8c8c; }

@media screen and (min-width: 1600px) {
  html, body {
    font-size: 22px; } }

@media screen and (min-width: 1400px) and (max-width: 1600px) {
  html, body {
    font-size: 20px; } }

@media screen and (min-width: 1200px) and (max-width: 1400px) {
  html, body {
    font-size: 18px; } }

@media screen and (min-width: 992px) and (max-width: 1200px) {
  html, body {
    font-size: 17px; } }

@media screen and (min-width: 768px) and (max-width: 992px) {
  html, body {
    font-size: 17px; } }

@media screen and (max-width: 768px) {
  html, body {
    font-size: 17px; } }

/* 定义适配基础单位 */
/* 定义px转rem规则 */
/* 定义间距尺寸 */
:root {
  --head-top: #f5f5f5;
  --head-tab: #ffffff;
  --head-name: #141414;
  --head-tips: #262626; }

.papers-box {
  position: relative;
  width: 100%;
  height: 375px;
  background-color: #212629;
  overflow: hidden; }
  .papers-box .papers-name {
    margin: 0 auto;
    margin-top: 1.75rem;
    margin-bottom: 1.125rem;
    width: 60.25rem;
    font-size: 0.875rem;
    font-weight: 400;
    color: #141414;
    color: #d9d9d9; }
  .papers-box .papers-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 auto;
    width: 61.75rem; }
    .papers-box .papers-list .papers-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 0.5rem;
      width: 14.3125rem;
      height: 4.75rem;
      background-color: var(--head-tab);
      border: 1px solid var(--head-tab); }
      .papers-box .papers-list .papers-item .papers-art {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-left: 0.625rem; }
        .papers-box .papers-list .papers-item .papers-art .papers-title {
          font-size: 0.75rem;
          font-weight: 400;
          color: #262626;
          font-weight: 500;
          color: #f5f5f5; }
        .papers-box .papers-list .papers-item .papers-art .papers-text {
          margin-top: 0.1875rem;
          font-size: 0.75rem;
          font-weight: 400;
          color: #262626;
          color: #d9d9d9; }
      .papers-box .papers-list .papers-item .papers-img {
        margin-right: 0.625rem;
        width: 1.75rem;
        height: 1.75rem; }
    .papers-box .papers-list .papers-item:hover, .papers-box .papers-list .papers-check {
      border: 1px solid var(--head-hover); }
      .papers-box .papers-list .papers-item:hover .papers-art .papers-title, .papers-box .papers-list .papers-check .papers-art .papers-title {
        color: var(--head-hover); }
      .papers-box .papers-list .papers-item:hover .papers-art .papers-text, .papers-box .papers-list .papers-check .papers-art .papers-text {
        color: var(--head-hover); }
