@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  font-family: -apple-system;
  font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;
  text-rendering: optimizeLegibility;
  -webkit-backface-visibility: hidden;
  -webkit-user-drag: none;
  /*font-family: "Microsoft YaHei", "微软雅黑", Helvetica, Tahoma,Arial, SimSun, "宋体", STXihei, "华文细黑", Heiti, "黑体", sans-serif;*/
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  /* 字体抗锯齿 */
  cursor: pointer; }

html, body {
  font-family: -apple-system;
  font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif; }

a {
  text-decoration: none; }

em {
  font-style: normal; }

input, textarea {
  margin: 0;
  padding: 0;
  border: none;
  outline: none; }

input[type=number] {
  -moz-appearance: textfield; }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; }

input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px white inset; }

::-webkit-input-placeholder, ::-webkit-textarea-placeholder {
  margin: 0;
  padding: 0;
  color: #8c8c8c; }

@media screen and (min-width: 1600px) {
  html, body {
    font-size: 22px; } }

@media screen and (min-width: 1400px) and (max-width: 1600px) {
  html, body {
    font-size: 20px; } }

@media screen and (min-width: 1200px) and (max-width: 1400px) {
  html, body {
    font-size: 18px; } }

@media screen and (min-width: 992px) and (max-width: 1200px) {
  html, body {
    font-size: 17px; } }

@media screen and (min-width: 768px) and (max-width: 992px) {
  html, body {
    font-size: 17px; } }

@media screen and (max-width: 768px) {
  html, body {
    font-size: 17px; } }

/* 定义适配基础单位 */
/* 定义px转rem规则 */
/* 定义间距尺寸 */
:root {
  --head-top: #f5f5f5;
  --head-tab: #ffffff;
  --head-name: #141414;
  --head-tips: #262626; }

.dialog-panel {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.38);
  z-index: 999; }

.dialog-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0px 0.125rem 0.1875rem 0.125rem rgba(0, 0, 0, 0.09);
  border-radius: 8px;
  background-color: #ffffff; }
  .dialog-box .dialog-title {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .dialog-box .dialog-title div {
      margin-left: 0.5rem;
      margin-right: 0.5rem;
      width: 1.375rem;
      height: 1.375rem; }
      .dialog-box .dialog-title div img {
        width: 100%;
        height: 100%; }
    .dialog-box .dialog-title h1 {
      padding: 1.125rem 0 0 0;
      font-size: 0.875rem;
      font-weight: 400;
      color: #141414;
      text-align: center; }
  .dialog-box .dialog-form {
    padding: 0 1.625rem 1.25rem 1rem; }
    .dialog-box .dialog-form .form-item {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-top: 1.75rem; }
      .dialog-box .dialog-form .form-item label {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        margin-right: 1.125rem;
        width: 2.875rem;
        font-size: 0.75rem;
        font-weight: 400;
        color: #262626; }
      .dialog-box .dialog-form .form-item .form-list {
        display: flex;
        flex-direction: column;
        position: relative; }
        .dialog-box .dialog-form .form-item .form-list input {
          position: relative;
          width: 10.5rem;
          padding: 0.375rem 0.5rem;
          font-size: 0.75rem;
          font-weight: 400;
          color: #262626;
          border: 1px solid #d9d9d9;
          background-color: #ffffff;
          border-radius: 3px; }
        .dialog-box .dialog-form .form-item .form-list input:focus {
          border: 1px solid #1890ff; }
        .dialog-box .dialog-form .form-item .form-list span {
          position: absolute;
          left: 0;
          bottom: -0.875rem;
          font-size: 0.625rem;
          color: #cf1322;
          font-weight: 400; }
      .dialog-box .dialog-form .form-item .list-warn input {
        border: 1px solid #cf1322; }
    .dialog-box .dialog-form .form-submit {
      margin: 1.625rem 0 0 3.625rem;
      padding: 0.4375rem 0;
      width: 11.5625rem;
      background-color: #1890ff;
      font-size: 0.75rem;
      font-weight: 400;
      color: #262626;
      color: #ffffff;
      text-align: center;
      border-radius: 22px; }
